

body {
	font-family: $font-family-sans-serif;
	transition: opacity .25s ease-out;
	opacity: 1;
  	&.fade-out{
  		opacity: 1;
	}
	&.admin-bar{
		opacity: 1;
	}
}

p {
	font-family: $font-family-libre-baskerville-regular;
}

h1,h2,h3,h4,h5,h6{
	font-family: $font-family-libre-baskerville-regular;
	font-style: italic;
}

h2{
	font-size: 2.5rem;
}


.admin-bar p.demo_store{
	top: 0;
}

.site{
	overflow: hidden;
}


/* LOGIN STYLES */
.u-column1{
	display: block;
}

.u-column2{
	display: none;
}

.login-hide .u-column2{
	display: block;
}

.login-hide .u-column1{
	display: none;
}

.password-input{
	width: 100%;
}

.btn{
	.btn-outline-primary{
		border-radius: 100px;
	}
}

/* Navbar Customizations */

.navbar{
	background: #f9f9f9;
	flex: 1;
	z-index: 999;
	.mobile-nav{
		position: absolute;
		.shiftnav-toggle-button{
   			background: none !important;
   			font-size: 1.5rem;
		}	
	}
	@media only screen and (max-width: 767px) {
		padding-left: 0;
		padding-right: 0;
	}
	.container{
		justify-content: center;
    	width: auto;
	}
	.quick-links-container{
		position: absolute;
		right: 20px;
			@media only screen and (max-width: 767px) {
			right: 15px;
		}
		img{
			max-width: 30px;
			border-radius: 100px;
		}
	}
	.navbar-collapse{
		width: 225px;
		justify-content: center;
		li{
			border-bottom: 1px solid rgba(000, 000, 000, 0.3);
		}
	}
	.navbar-nav{
		align-items: center;
		a{
			&:hover{
				text-decoration: none;
			}
			.icon-user{
				font-size: 1.5rem;
			}
		}
		.xoo-wsc-icon-cart1{
			&:hover{
				color: $hennepens-green;
			}
		}
	}
	.navbar-brand{
		margin: 0 auto;
    	padding: 30px;
    	text-align: center;
    	display: flex;
    	flex-direction: column;
    	@media only screen and (max-width: 767px) {
    		padding: 10px 30px;
    		max-width: 92%;
    	}
		#logo-svg{
			transition: all .25s ease-out;
			max-height: 60px;
			filter: invert(1) sepia(0%) saturate(0%) hue-rotate(0) brightness(100) contrast(100);

		}
		.tagline{
			margin-top: -5px;
			letter-spacing: 4px;
			font-size: 1rem;
			font-family: $font-family-superior-regular !important;
		}
	}
	a{
		color: #000;
		font-family: $font-family-libre-baskerville-regular;
		&:hover{
			color: $hennepens-green;

		}
	}
}


.woocommerce-store-notice.demo_store {
	position: relative;
	background: #eee;
	border-bottom: 1px solid #ddd;
	box-shadow: none;
	font-weight: bold;
	font-family: $font-family-sans-serif;
	.woocommerce-store-notice__dismiss-link {
		display: none;
	}
	color: $hennepens-green;
}


.menu-footer-menu-contact-container{
	.menu{
		padding: 0;
		list-style: none;
		li{
			padding: 0 10px;
		}
		@media only screen and (max-width: 767px) {
			text-align: center;
			display: none;
		}
	}
	@media only screen and (max-width: 767px) {
		display: none;
	}
}

.xoo-wsc-sc-bki{
	margin-left: 15px;
}

/* HOME PAGE CUSTOMIZATIONS */

.home,.page-our-mission,.page-about-us,.page-sell-wholesale, .page-arc-partnership{
	.navbar{
		background: none;
		position: absolute;
		z-index: 999;
		width: 100%;
		.navbar-collapse{
			li{
				border-bottom: 1px solid rgba(255, 255, 255, 0.3);
			}
		}
		.navbar-brand{
			#logo-svg{
			filter: invert(0) sepia(0%) saturate(0%) hue-rotate(0) brightness(100) contrast(100);

			}
			.tagline{
				color: #fff;
			}
		}
		a{
			color: #fff;
			&:hover{
				color: $hennepens-green;
			}
		}
	}
	.xoo-wsc-sc-bki{
		color: #fff;

	}
	.woocommerce-store-notice{
		display: none !important;
	}
	.partial-content-space{
		h2{
			@media only screen and (min-width: 768px) {
				max-width: 65%;
			}
		}
		p{
			@media only screen and (min-width: 768px) {
				max-width: 65%;
			}
		}
		&.right{
			h2{
				@media only screen and (min-width: 768px) {
					float: right;
				}
			}
			p{
				@media only screen and (min-width: 768px) {
					float: right;
				}
			}
		}
		.max-width-65{
			@media only screen and (min-width: 768px) {
				float: right;
				max-width: 65%;
			}
		}
	}
	.min-height-img-col{
		@media only screen and (max-width: 768px) {
			min-height: 300px;
		}
	}
}
.max-width-65{
	.fl-col-content.fl-node-content{
		@media only screen and (min-width: 768px) {
			max-width: 65%;
		}
	}
	.fl-col-group-group-nested{
		.fl-col-content.fl-node-content{
			@media only screen and (min-width: 768px) {
				max-width: 100% !important;
			}
		}
	}
}


.map-column{
	.fl-module.fl-module-map{
		@media only screen and (min-width: 768px) {
			position: absolute;
	    	top: 0;
	    	left: 0;
	    	width: 50%;
	    	z-index: -1;
    	}
	}
}
.videoWrapper {
  position: relative;
  padding-bottom: 56.25%; /* 16:9 */
  height: 0;
  &:after{
  	content: '';
  	position: absolute;
  	width: 100%;
  	height: 100%;
  	border: 10px solid #fffae1;

  }
}
.videoWrapper iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.collage{
	video{
		width: 100%;
		max-width: 100%;
		display: block;
	}
}

#hero-header{
	.fl-heading{
		font-family: $font-family-superior-regular !important;
		font-size: 6em;
		letter-spacing: 2px;
		margin-bottom: 50px;
	    text-align: center;
	    font-style: italic;
	    transform: rotate(-5deg);
	    @media only screen and (max-width: 767px) {
			font-size: 3.75rem;
		}
	}
}

#home-page-hero{
	.fl-row-content-wrap{
		min-height: 80vh;
	}
	.fl-button{
  		transform: rotate(-5deg) skewX(-20deg);
	}
	.fl-button-text{
		font-family: $font-family-superior-regular !important;
		transform: skewX(20deg);
		text-transform: lowercase;
		letter-spacing: 4px;
	}
}

.path-to-wellness{
	padding: 10px;
	border: 1px solid #000;
	@media only screen and (max-width: 767px) {
		border: 0;
	}
}


/* Footer Edits */

#wrapper-footer{
	background-color: #e9ecef;
	padding-top: 0;
	background-image: url('../images/newsletter-bg-2.jpg');
	border-top: 10px solid #000;
}
#wrapper-footer-full{
	padding: 50px 0;
	background: none;
}

#menu-main-footer-menu{
	column-count: 2;
	list-style: none;
	padding: 0;
	@media only screen and (max-width: 767px) {
		column-count: 1;
		text-align: center;
	}
}

.disclaimer{
	font-size: .7rem;
	text-align: center;
	border: 1px solid #ccc;
	padding: 20px;
}

.site-info ul{
	list-style: none;
	display: flex;
	padding: 0;
	text-align: center;
	li{
		padding-right: 10px;
		&:last-child{
			padding-right: 0;
		}
	}
}

.social-cc{
	.fa{
		font-weight: inherit !important;
		font-family: FontAwesome;

	}
}


/* Product Categories */

.cat-header{
	width: 100%;
	height: 400px;
	background-size: cover;
}

.tax-product_cat {
	.content-area{
		padding: 0;
	}
	#woocommerce-wrapper .container:first-child {
		max-width: 100%;
		
	}
	.wrapper{
		padding: 0;
	}
}
.added_to_cart.wc-forward{
	display: none !important;
}
.products{
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	@media only screen and (max-width: 767px) {
		margin-right: -10px !important;
		margin-left: -10px !important;
	}
	li{
		position: relative;
		display: flex;
		flex-direction: column;
		img{
			margin-bottom: 0 !important;
		}
		.add_to_cart_button{
			width: 150px;
			margin: 20px auto 0 auto;
			border-radius: 100px;
			color: #000;
			background-color: #fff;
			border: 2px solid #000;
			.woocommerce-button{
				display: inline-block !important;				
			}

			&:hover{
				background: #000;
				color: #fff;
				border-color: #fff;
			}
		}
		.woocommerce-loop-product__title{
			padding: 0;
			flex-grow: 1;
			span:first-child{
				font-family: $font-family-libre-baskerville-regular;
    			font-size: 1.5rem;
    			font-weight: 700;
    			margin-bottom: 8px;
    			display: block;
    			@media only screen and (max-width: 767px) {
					font-size: 1.35rem;
				}
			}
			span:nth-child(2){
				display: block !important;
				em{
					position: relative;
					&:before,
					&:after {
						position: absolute;
						vertical-align: middle;
					    display: inline-block;
					    content: "";
					    border-top: 1px solid rgba(000, 000, 000, 0.3);
					    width: 12%;
					    margin: 0 1rem;
					    top: 50%;
					    @media only screen and (max-width: 767px) {
							
						}
					}
					&:before{
						left: -24%;
						 @media only screen and (max-width: 767px) {
							
						}
					}
				}
			}
			span:last-child{
				padding: 5px 0;
				background: #D4AF37;
				background: linear-gradient(to bottom, #D4AF37 0%, #C5A028 100%);
				background: -webkit-gradient(linear, left top, left bottom, color-stop(0%, #D4AF37), color-stop(100%, #C5A028));
				background: -webkit-linear-gradient(top, #D4AF37 0%, #C5A028 100%);
				background: -moz-linear-gradient(top, #D4AF37 0%, #C5A028 100%);
				background: -o-linear-gradient(top, #D4AF37 0%, #C5A028 100%);
				background: -ms-linear-gradient(top, #D4AF37 0%, #C5A028 100%);
				filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#D4AF37', endColorstr='#C5A028', GradientType=0);
				border: 1px solid #B69119;
				box-shadow: inset 0 1px 0 #E3BE46;
				-webkit-box-shadow: inset 0 1px 0 #E3BE46;
				-moz-box-shadow: inset 0 1px 0 #E3BE46;
			    color: #fff;
			    display: inline-block;
			    margin-top: 10px;
			    font-size: .75rem;
			    &:before{
			    	content: '• ';
			    	padding-right: 4px;
			    	padding-left: 10px;
			    }
			    &:after{
			    	content: ' •';
			    	padding-left: 4px;
			    	padding-right: 10px;
			    }
			}
		}
		.single_add_to_cart_button{
			width: 150px;
			margin: 20px auto 0 auto;
			border-radius: 100px;
			background-color: #fff;
			border: 2px solid #000;
			&:hover{
				background: #000;
				color: #fff;
				border-color: #fff;
			}
		}
		.product-info{
			display: flex;
			text-align: center;
			flex-flow: column;
    		flex-grow: 1;
    		.woocommerce-loop-product__link{
    			display: flex;
    			flex-direction: column;
    			flex-grow: 1;
    			.price{
    				font-family: $font-family-libre-baskerville-regular;
    				font-style: italic;
    				color: #000 !important;
					margin-top: 0 !important;
					margin-bottom: -10px !important;
					font-size: 1.5rem !important;
					padding-top: 10px;
				}
    		}

		}
		.wcsatt-sub-options{
			font-style: italic;
		}
		.wcsatt-sub-discount{
			font-style: italic;
		}
		.wcsatt-dash{
			display: none;
		}
		.subscription-message{
			width: 100%;
		    text-align: center;
		    font-size: .9rem;
		    font-style: italic;
		    color: #356e3e;
		    margin-top: 10px;
		    padding: 0;
		    border: 0;
		}
	}
}

li.product:nth-child(2n), .woocommerce-page ul.products[class*=columns-] li.product:nth-child(2n){
	@media only screen and (max-width: 767px) {
		padding-left: 10px;
	}
}
.woocommerce ul.products[class*=columns-] li.product, .woocommerce-page ul.products[class*=columns-] li.product{
	padding-left:10px;
}


.stamp-group{
	img{
		float: left;
	    margin-bottom: 20px;
    	margin-right: 5px;
    	@media only screen and (max-width: 767px) {
			float: none;
			margin: 0 auto;
		}
    }

}



/* Single Product Template */

.single-product{
	#woocommerce-wrapper{
		padding-top: 0;
	}
	.purchase-options{
		max-width: 100% !important;
		label{
			&.subscription-container{
				width: auto;
				flex-grow: 1;
				padding: 15px 15px 15px 0;
				display: flex;
				align-items: center;
			}
		}
		li{
			padding: 0 !important;
			display: flex;
			flex-wrap: wrap;
			input{
				margin-left: 15px;
				width: 1.25rem;
				height: 1.25rem;
			}
			&.selected{
				border-radius: 8px;
			}
		}
		.help-icon{
			@media only screen and (max-width: 767px) {
				margin-left: 8px;
				margin-right: 0;
			}
		}
	}
	.woocommerce-product-gallery{
		.flex-control-nav{
			display: flex;
			justify-content: center;
			li{
				max-width: 100px;
				img{
					max-width: 100px;
				}
			}
		}
	}
}

.prev_next_buttons {
line-height: 40px;
margin-bottom: 20px;
}
 
.prev_next_buttons a[rel="prev"], .prev_next_buttons a[rel="next"] {
display: block;
}
 
.prev_next_buttons a[rel="prev"] {
float: right;
}
 
.prev_next_buttons a[rel="next"] {
float: left;
}
 
.prev_next_buttons::after {
content: '';
display: block;
clear:both;
}

.woocommerce-variation-price{
	.price{
		.amount{
			display: block;
    		padding-bottom: 5px;
    		font-size: 1.25rem;
			font-family: $font-family-libre-baskerville-regular;
			font-style: italic;
			bdi{
				font-size: inherit;
			}
		}
	}
}

.single_variation_wrap{
	button.button.single_add_to_cart_button{
		padding: 20px;
	    min-width: 200px;
	    font-size: 1.25rem;
	    background: #356e3e;
	    border: 4px solid #75BD80;
	    border-radius: 100px;
	    color: #fff;
	    text-transform: uppercase;
	    transition: 100ms all ease;
	    @media only screen and (max-width: 767px) {
			width: 100%;
		}
	    &.loading{
	    	padding-right: 50px;
	    	&:after{
	    		top: auto;
	    	}
	    }
	    &:hover{
	    	background: #75BD80;
	    	border-color: #356e3e;
	    }
	}
}


